<template>
    <div class="card">
        <div class="card-header">
            <h2></h2>
        </div>
        <div class="card-body">
            <div class="table-responsive">
                <table class="table table-striped table-vmiddle ">
                    <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th>/	</th>
                        <th>/</th>
                        <th>/	</th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="item in list">
                        <td>{{item.createdAt | parseTime}}</td>
                        <td>{{item.pair}}</td>
                        <td>{{item.order_side == 1 ? '' : ''}}</td>
                        <td>{{item.price}}<br/>{{item.deal_price}}</td>
                        <td>{{item.amount}}<br/>{{item.deal_amount}}</td>
                        <td>{{item.money}}<br/>{{item.deal_money}}</td>
                        <td><a @click="cancelOrder(item.id)"></a></td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import _ from 'underscore'
	import service from '@/coinpark';
	import toastr from './toastr';
	export default {
		props: {
			_list : Array
		},

        data : function () {
            return {
                list : [],
                orderStatus: {
                    1: '',
                    2: '',
                    3: '',
                    4: '',
                    5: '',
                    6: ''
                },
            };
        },

		watch: {
			_list (val) {
				this.list = val;
            }
        },

		methods: {
			cancelOrder(id) {
				var me = this;
				service.cancelOrder(id).then(function (res) {
					console.log('cancelOrder', res)
                    toastr.success('');
					me.$emit('cancelOrder')
				});
			},

		},
        mounted () {

        }
	}
</script>

<style scoped="">
    a {
        cursor: pointer;;
    }
</style>



